import React, {useState, useEffect} from 'react';
import "./App.css";
function App() {

  const [ data, setData] = useState([]);

  const apiGet = () => {
    fetch("https://restcountries.eu/rest/v2/region/asia")
    .then((response) => response.json())
    .then((json) => {
      console.log(json);
      setData(json);
    });
  };
    
  
  return (
    <div className="App">
      <span>Hello Get to know more Countries</span> <button onClick={apiGet}>Click!</button>
      {/*<pre>{JSON.stringify(data, null, 2)}</pre>*/}
      <div className="data">
        <ul>
          {data.map((item) => 
          <li key={item.id}>{item.name}<br/>
              {item.capital}<br/>
              <img style={{height:"50px"}} src={item.flag}></img><br/>
              {item.region}<br/>
              {item.subregion}<br/>
              {item.population}<br/>
              {item.borders}<br/>
              {item.language}<br/>
          </li>)}
        </ul>
      </div>
    </div>
  );
}

export default App;
